.profile__discription {
  margin: 0;
  padding: 0;
  width: 294px;
  font-family: 'Inter', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media (max-width: 690px) {
  .profile__discription {
    text-align: center;
  }
}

@media (max-width: 405px) {
  .profile__discription {
    font-size: 14px;
    line-height: 17px;
    width: 189px;
    text-align: center;
  }
}
