.header {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
  height: 74px;
  margin: 45px auto 36.25px auto;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  box-sizing: border-box;
}

@media (max-width: 405px) {
  .header {
    height: 56px;
    margin-top: 28px;
    margin-bottom: 38.25px;
  }
}
