.login-bar__button {
  padding: 0;
  margin: 0;
  margin-top: 6px;
  max-height: 22px;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  color: #ffffff;
  cursor: pointer;
  font-style: normal;
  font-family: 'Inter', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-decoration: none;
  vertical-align: text-top;
}

.login-bar__button:hover {
  opacity: 0.6;
}

@media (max-width: 460px) {
  .login-bar__button {
    font-size: 16px;
  }
}

@media (max-width: 360px) {
  .login-bar__button {
    font-size: 14px;
  }
}
